import React, { memo } from "react";
import {
  BarChart,
  Bar,
  Brush,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
} from "recharts";
import { useData } from "../../store";

const options = {
  hour: "numeric",
  minute: "numeric",
  second: "numeric",
};

function Timeline() {
  const data = useData((state) => state.data);

  const barsData = data.features
    .map((el) => {
      return {
        date: el.properties.time,
        mag: el.properties.mag > 0 ? el.properties.mag : 0,
        place: el.properties.place,
        coordinates: el.geometry.coordinates.slice(0, 2).reverse(),
      };
    })
    .reverse();

  const CustomTooltip = ({ active, payload }) => {
    if (active && payload && payload.length) {
      return (
        <div className="bg-zinc-800 border-cyan-100 text-white p-4">
          <p>
            Date:{" "}
            {new Intl.DateTimeFormat("default", options).format(
              new Date(payload[0].payload.date),
            )}
          </p>
          <p>Magnitude: {payload[0].payload.mag}</p>
          <p>{payload[0].payload.place}</p>
        </div>
      );
    }

    return null;
  };

  const brush = (e) => {
    return new Intl.DateTimeFormat("default", options).format(new Date(e));
  };

  return (
    <ResponsiveContainer height={"85%"}>
      <BarChart
        data={barsData}
        margin={{
          top: 0,
          right: 20,
          left: -30,
          bottom: 5,
        }}
      >
        <CartesianGrid strokeDasharray="0 0" stroke="0" />
        <XAxis
          dataKey="date"
          domain={[barsData[0].time, barsData.at(-1).time]}
          type="number"
          tick={false}
        />
        <YAxis domain={[0, 10]} tick={true} />
        <Tooltip
          wrapperStyle={{ outline: "none" }}
          content={<CustomTooltip />}
        />

        <Bar dataKey="mag" fill="#15CAE9" barSize={20} />
        <Brush
          dataKey="date"
          height={12}
          stroke="rgba(0,255,255,0.2)"
          fill="#262626"
          tickFormatter={brush}
          startIndex={Math.round(barsData.length / 1.2)}
        />
      </BarChart>
    </ResponsiveContainer>
  );
}

export default memo(Timeline);
